import React, { useState, useEffect } from "react";
import {
  collection,
  addDoc,
  query,
  getDocs,
  doc,
  onSnapshot,
  setDoc,
} from "firebase/firestore";
import { ref, uploadString, getDownloadURL } from "firebase/storage";
import { dbService, storage } from "fbase.js";
import Jweet from "components/Jweet";
import NweetFactory from "components/JweetFactory";

const Home = ({ userObj }) => {
  const [jweets, setJweets] = useState([]);

  // const getJweets = async () => {
  //   const dbJweets = await getDocs(collection(dbService, "jweets"));
  //   dbJweets.forEach((doc) => {
  //     const jweetObject = {
  //       ...doc.data(),
  //       id: doc.id,
  //     };
  //     setJweets((prev) => [jweetObject, ...prev]);
  //   });
  // };  // 구식방법이라 하고 지우심
  useEffect(() => {
    // getJweets();
    const q = query(collection(dbService, "jweets"));
    onSnapshot(q, (snapshot) => {
      const jweetArray = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
      setJweets(jweetArray);
      // 여기까지가 새로운 방법
    });
  }, []);
  return (
    <div>
      <NweetFactory userObj={userObj} />
      <div>
        {jweets.map((jweet) => (
          <Jweet
            key={jweet.id}
            jweetObj={jweet}
            isOwner={jweet.creatorId === userObj.uid}
          />
        ))}
      </div>
    </div>
  );
};

export default Home;
